// ==================================================
// Images
// ==================================================


// Standard figures
// ------------------------------
figure {
	display: table;
	margin-left: auto;
	margin-right: auto;
	padding: $space;
	text-align: center;
	background: $grey-lighter;
	box-sizing: border-box;
	
	img {
		display: block;
		margin: 0 auto;
	}
}
figcaption {
	margin: 1rem 0 0;
	box-sizing: border-box;
}


// Image block [gutenberg]
// ------------------------------
.wp-block-image {
	margin: 0 auto $space;
	
	figcaption {
		display: block !important;
		margin: 1rem 0 0;
		font: inherit;
		color: inherit;
		text-align: inherit;
	}
	
	// Size and alignment variations
	&.is-resized {
		margin-left: auto;
		margin-right: auto;
	}
	.aligncenter {}
	.alignleft {
		@extend .float-left;
	}
	.alignright {
		@extend .float-right;
	}
	&.alignwide {}
	&.alignfull {}
}


// Gallery block [gutenberg]
// ------------------------------
.wp-block-gallery {
	.blocks-gallery-item {
		flex-grow: 0;
	}
	figure {
		padding: 0;
	}
	figcaption {
		@include font-size-small;
	}
	
	&.alignleft {
		@extend .float-left;
	}
	&.alignright {
		@extend .float-right;
	}
}
